@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';
.alerta {
    padding: 1.5rem 1.5rem 1.5rem 3rem;
    margin: 1rem 0;
    text-transform: uppercase;
    font-weight: v.$bold;
    font-size: 1.2rem;
    &__error {
        background-color: color.adjust(v.$rojo, $lightness: 60%);
        border-left: .5rem solid v.$rojo;
        color: v.$rojo;
    }
    &__exito {
        background-color: color.adjust(v.$verde, $lightness: 40%);
        border-left: .5rem solid v.$verde;
        color: color.adjust(v.$verde, $lightness: -20%);
    }
}
